@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');

.usercontainer {
    width: 100%;
    height: 100%;
    background-color: orange;
    padding: 10px;
    border-radius: 15px;
}

.usercontainer ul, li {
    margin-left: 0;
    padding-left: 0;
}

.usercontainer li {
    list-style-type: none;
}

.message span {
    color: grey;
    font-weight: normal;
    margin-right: 10px;
    font-family: 'Roboto Mono', monospace;
}

.message {
    font-weight: bold;
}

.input-group {
    margin-top: 10px;
}